@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Playfair+Display:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Playfair+Display:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Playfair+Display:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navContainer{top:0;position:sticky;height:100%;background-color:#90A68D}.navContainer nav{font-family:'Lato', sans-serif;font-size:1.5em;margin:0 auto}.navContainer nav ul{margin:0 auto;padding:0}.navContainer nav ul li{display:inline-block;padding-left:1em}.navContainer nav ul a,.navContainer nav ul h1{color:#F2E5D5;transition:-webkit-text-decoration 0.5s, -webkit-text-decoration-color 1s;transition:text-decoration 0.5s, text-decoration-color 1s;transition:text-decoration 0.5s, text-decoration-color 1s, -webkit-text-decoration 0.5s, -webkit-text-decoration-color 1s}.navContainer nav ul h1{font-weight:bolder}.navContainer nav ul a:hover{text-decoration:underline;-webkit-text-decoration-color:#D99B84;text-decoration-color:#D99B84}

.project-card{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));max-width:50%;margin:0 auto;padding-bottom:2%}.project-card img{max-width:100%;height:100%;justify-self:start;object-fit:fill;overflow:hidden}.project-card span{font-size:2em;display:grid;grid-template-columns:1fr 1fr;width:50%;margin:0 auto}.project-card .projectImage{justify-self:start}.project-card a{color:white;transition:0.2s}.project-card a:hover{color:#fdb5af}#one.projectInfo{background-color:#9ca68d}#two.projectInfo{background-color:#8da6a6}#three.projectInfo{background-color:#8da69e}#four.projectInfo{background-color:#93a68d}.projectInfo{padding:2% 70px;font-family:'Open Sans', sans-serif}.projectInfo h1{font-family:'Playfair Display', serif}#projectTitle h1{text-transform:uppercase;font-family:'Playfair Display', serif;color:#82896A}

.aboutInfo{display:flex;flex-wrap:nowrap;align-content:center;justify-content:center;align-items:center;width:80%;margin:0 auto;grid-gap:40px;gap:40px;margin-top:2%}.aboutInfo img{width:50%}.aboutInfo .about-info{width:60%;text-align:left}#about h1{margin-top:5%;font-family:'Playfair Display', serif;text-transform:uppercase;color:#e4837f}

#contact h1{font-family:'Playfair Display', serif;text-transform:uppercase;color:#82896A}#contact h2{color:#67a19e;font-family:'Playfair Display', serif}#contact .contactCards{display:grid;align-content:center;justify-content:center;grid-template-columns:repeat(4, 1fr);width:70%;margin:0 auto;grid-gap:5%;gap:5%}#contact .contactCards div{padding:10% 5%;box-shadow:5px 5px #D99B84;background:#fffdfd;border-radius:10px}#contact .contactCards div a{font-family:'Open Sans', sans-serif;color:#82896A}#contact .contactCards div svg{color:#565E50}

#skillsSection h1{font-family:'Playfair Display', serif;text-transform:uppercase;padding-top:3%;color:#e4837f}#skillsSection h2{font-family:'Playfair Display', serif;color:#67a19e}#skillsSection .currentTechStack p{font-size:3em;margin:0}#skillsSection .currentTechStack img{width:50px;margin:0 1em}#skillsSection .currentTechStack svg{margin:0 1em}.wishList{margin-bottom:5%}.wishList h1{margin-top:0;padding-top:20px}.wishList svg{font-size:3em;margin:0 1em}

#landingPage img{max-width:400px;min-width:300px;height:auto}#landingPage .landingPageContent{display:flex;justify-content:space-evenly;margin:0 auto;width:80%;align-items:center;flex-wrap:wrap}#landingPage .landingPageContent h1{font-family:'Playfair Display', serif;text-transform:uppercase;font-size:5em;margin-bottom:0;color:#565E50;text-shadow:2px 2px #D99B84}#landingPage .landingPageContent h2{font-family:'Open Sans', sans-serif;text-align:left;font-size:2em;line-height:1.5;color:#353d2f}#landingPage .landingPageContent h2 span{text-shadow:2px 2px #D99B84}

#about,#landingPage,#skills{height:100vh;font-family:'Open Sans', sans-serif}#landingPage{padding-top:5%}#components{display:flex;flex-direction:column;flex-flow:column wrap}#landingPage,#projects,#contact{background:#f8f1e8}#about,#skills{background:#fffaf3}#contact{height:70vh;padding-top:5%}#projects{height:100%;padding-bottom:5%;padding-top:5%}button{background-color:#F2B279;border:none;padding:10px;color:white;border-radius:30px;transition:0.3s}button:hover{box-shadow:5px 5px #D99B84}

